
.Certifications{
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.certcontent{
    height:75vh;
    width: 30%;
    border-radius: 20px;
    font-size: 25px;
    color: white;
    font-family: "IM Fell English";
}
.certcontent li{
    margin-top: 20px;
}
.certdisplay{
    height:75vh;
    width: 70%;
    border-radius: 20px;
    overflow: hidden;
    border-radius: 20px;
    margin-top: 10px;
    margin-right: 10px;
}

.certimg{
    height: fit-content;
    width: 100%;
}

.display{
    height: 70vh;
    width: 100%;
    border-radius: 20px;
    overflow: hidden;
    background-color: white;
}

.certtitle{
    text-align: center;
}